@import '~Typography';

.radio-buttons {
  &__title {
    margin-bottom: 12px;
  }

  &__item {
    display: flex;
    align-items: center;

    &:first-child {
      margin-right: 20px;
    }
  }

  &__items {
    display: flex;
  }

  &__radio {
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    margin-right: 10px;
    margin-left: 0;
    border: 1px solid $dark-shade-25;
    border-radius: 10px;
    cursor: pointer;

    &:checked {
      line-height: 14px;
      position: relative;
      color: $color-primary;
      border: 1px solid $color-primary;
    }

    &:checked:after {
      position: absolute;
      top: 3px;
      left: 3px;
      width: 12px;
      height: 12px;
      content: '';
      background: $gradient-primary;
      border-radius: 10px;
    }

    &:checked ~ label {
      color: $dark-shade-75;
    }
  }

  &__label {
    @include typo-body;
    color: $dark-shade-50;
    cursor: pointer;
  }
}
